import {
    Button, Grid, Link, Typography, DialogContentText, Container,
    Dialog, DialogTitle, DialogContent, TextField, DialogActions,
} from '@material-ui/core'
import {
    MuiPickersUtilsProvider, KeyboardDatePicker,
} from '@material-ui/pickers'
import {footerLinks} from './startPageData'
import {useStyles} from './startPageStyles'
import {useState} from "react"
import EmailOutlinedIcon from '@material-ui/icons/EmailOutlined'
import ChatOutlinedIcon from '@material-ui/icons/ChatOutlined'
import DateFnsUtils from '@date-io/date-fns'
import 'date-fns'


export default function StartPage() {
    const styles = useStyles()
    document.body.style.minHeight = `${window.innerHeight}px`

    const [open, setOpen] = useState(false)
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [selectedDate, setSelectedDate] = useState<Date | null>(new Date())

    const handleClickOpen = () => {
        setOpen(true)
    }
    const handleClose = () => {
        setOpen(false)
    }

    const handleDateChange = (date: Date | null) => {
        setSelectedDate(date)
    }

    // @ts-ignore
    const handleSubmit = (e) => {
        e.preventDefault()
        alert(`This will be your registration data: ${name},  ${email}, ${password}, ${selectedDate}`)
    }

    return (
        <Container maxWidth={false} className={styles.container}>
            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Sign Up</DialogTitle>

                <DialogContent>
                    <Grid container justifyContent='space-between' direction='column' style={{minHeight: '380px'}}>
                        <DialogContentText>
                            Create an account
                        </DialogContentText>

                        <TextField
                            autoComplete="name" name="name" variant="outlined"
                            required fullWidth label="Name" autoFocus value={name}
                            onChange={(e) => setName(e.target.value)}
                        />

                        <TextField
                            autoComplete="email" name="email" variant="outlined"
                            required fullWidth label="Email" value={email}
                            onChange={(e) => setEmail(e.target.value)}
                        />

                        <TextField
                            autoComplete="password" name="password" variant="outlined"
                            required fullWidth label="Password" value={password}
                            onChange={(e) => setPassword(e.target.value)}
                        />

                        <Grid container item direction='column' justifyContent='center'>
                            <Typography variant='subtitle2'>
                                Birth date
                            </Typography>

                            <Typography variant='body2'>
                                This information will not be publicly available. Confirm your age even if this
                                account is for a company, pet, etc.
                            </Typography>
                        </Grid>

                        <Grid container item justifyContent='space-between' alignItems='center'>
                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                <KeyboardDatePicker
                                    id="date-picker-dialog"
                                    label="Choose your birth date"
                                    format="MM/dd/yyyy"
                                    value={selectedDate}
                                    onChange={handleDateChange}
                                    KeyboardButtonProps={{
                                        'aria-label': 'change date',
                                    }}
                                />
                            </MuiPickersUtilsProvider>
                        </Grid>
                    </Grid>
                </DialogContent>

                <DialogActions>
                    <Button onClick={handleClose} color="primary">
                        Cancel
                    </Button>

                    <Button onClick={handleSubmit} color="primary">
                        Sign Up
                    </Button>
                </DialogActions>
            </Dialog>

            <Grid
                container
                direction="column"
                justifyContent="space-between"
                alignItems="center"
            >
                <Grid
                    container item
                    direction="row"
                    justifyContent='center'
                    alignItems='center'
                    className={styles.startPageContent}
                >
                    <Grid
                        item
                        lg={7}
                        className={styles.startPageBackground}
                    >
                    </Grid>

                    <Grid
                        container item
                        lg={5}
                        direction='column'
                        justifyContent='space-around'
                        alignItems='flex-start'
                        className={styles.startPageBaseInfo}
                    >
                        <ChatOutlinedIcon className={styles.appIcon}/>

                        <Grid container item>
                            <Typography variant='h3'>
                                Welcome to Social Network App
                            </Typography>

                            <Typography variant='h4' className={styles.subHeadline}>
                                Join now!
                            </Typography>
                        </Grid>

                        <Grid
                            container item
                            direction='column'
                        >
                            <Button variant="outlined" size='large' disabled
                                    startIcon={
                                        <img alt='google-icon' className={styles.googleSignUpIcon}
                                             src="https://upload.wikimedia.org/wikipedia/commons/5/53/Google_%22G%22_Logo.svg"/>
                                    }
                                    className={styles.googleSignUp}>
                                Sign up with Google
                            </Button>

                            <Button variant="outlined" size='large' className={styles.emailSignUp}
                                    onClick={handleClickOpen} startIcon={<EmailOutlinedIcon/>}>
                                Sign up with Email
                            </Button>

                            <Typography variant='subtitle1' className={styles.confirmationText}>
                                By registering, you agree to the <Link href='#'>Terms of Service</Link> and <Link
                                href='#'>Privacy Policy</Link>, as well as the <Link href='#'>Cookie Policy</Link>.
                            </Typography>
                        </Grid>

                        <Typography variant='subtitle1' className={styles.ifAccountExists}>
                            Already have an account? <Link href='#'>Sign In</Link>
                        </Typography>
                    </Grid>
                </Grid>

                <Grid
                    container item
                    direction='row'
                    justifyContent='center'
                    alignItems='center'
                    className={styles.startPageFooter}
                >
                    <Grid
                        container item
                        lg={7}
                        justifyContent="space-around"
                    >
                        {Object.keys(footerLinks).map(function (key) {
                            return <Link variant='body2' className={styles.startPageFooterText}
                                         href={footerLinks[key]}
                                         color='inherit'>{key}</Link>
                        })}
                    </Grid>

                    <Grid
                        container item
                        justifyContent='center'
                    >
                        <Typography variant='body2' className={styles.startPageFooterText}>
                            Social Network App â€“ {new Date().getFullYear()}
                        </Typography>
                    </Grid>
                </Grid>
            </Grid>
        </Container>
    )
}
